import React, { Component } from "react";
import axios from "axios";
import { /*Button,*/ Pagination } from "react-materialize";
import Modal from "react-modal";
//import $ from "jquery";

const page_size = 2000;

//const server_path_localhost = "http://localhost:3001";
const heroku_path = "https://english-book-reader.herokuapp.com";
const server_path = heroku_path;

class App extends Component {
  constructor() {
    super();
    this.state = {
      output: "",
      input: "",
      currentPage: 1,
      modalIsOpen: false
    };
    this.translate = this.translate.bind(this);
    this.findWord = this.findWord.bind(this);
    this.first = this.first.bind(this);
    this.select = this.select.bind(this);
    this.handleHideModal = this.handleHideModal.bind(this);

    this.openModal = this.openModal.bind(this);
    // this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);

    // this.openPdf('books/book1.txt')
    this.openBook("filename");
  }

  componentDidMount() {
    Modal.setAppElement("#root");
    // Modal.setAppElement("#root");
  }

  openModal() {
    this.setState({ modalIsOpen: true });
  }

  /*afterOpenModal() {
    // references are now sync'd and can be accessed.
    this.subtitle.style.color = "#f00";
  }*/

  closeModal() {
    this.setState({ modalIsOpen: false });
  }

  handleHideModal() {
    this.setState(state => {
      return {
        openModal: false
      };
    });
  }

  first() {
    // const size = this.state.input.length;
    // const firstPage = this.state.input.slice(0,page_size)
    // const page_number = size/page_size
    console.log("first");
    this.setState(state => {
      return {
        // currentText: firstPage,
        // pageNumber:page_number,
        currentPage: 1
      };
    });
  }

  select(i) {
    this.setState(state => {
      return {
        currentPage: i
      };
    });
  }

  /*translate(textToTranslate, language) {
    axios
      .get(
        "https://translate.yandex.net/api/v1.5/tr.json/translate?key=" +
          key +
          "&lang=" +
          language +
          "&text=" +
          textToTranslate
      )
      .then(response => {
        var output = response.data.text[0];
        this.setState({ output });
        this.openModal();
      })
      .catch(error => console.log(error));
  }*/

  translate(textToTranslate, language) {
    axios
      .get(server_path + "/api/get-translate/" + textToTranslate)
      .then(response => {
        var output = response.data.data;
        this.setState({ output });
        this.openModal();
      })
      .catch(error => console.log(error));
  }

  openPdf(filename) {
    var that = this;
    axios
      .get("http://localhost:3001/api/get-book_pdf")
      .then(function(response) {
        let resp = response.data;
        console.log("resp", resp);
        that.setState({
          input: resp.data
        });
      });
  }

  openBook(filename) {
    var that = this;
    //axios.get("http://localhost:3001/api/get-book1").then(function(response) {
    axios.get(server_path + "/api/get-book1").then(function(response) {
      let resp = response.data;
      console.log("resp", resp);
      that.setState({
        input: resp.data
      });
      that.first();
    });
  }

  findWord(event) {
    event.preventDefault();
    let s = window.getSelection();
    var range = s.getRangeAt(0);
    var node = s.anchorNode;
    while (range.toString().indexOf(" ") !== 0) {
      range.setStart(node, range.startOffset - 1);
    }
    range.setStart(node, range.startOffset + 1);
    do {
      range.setEnd(node, range.endOffset + 1);
    } while (
      range.toString().indexOf(" ") === -1 &&
      range.toString().trim() !== "" &&
      range.endOffset < node.length
    );
    var str = range.toString().trim();
    //   alert(str);

    var language = "ru"; // this.refs.language.value;
    this.translate(str, language);
  }

  render() {
    const cpg = this.state.currentPage;
    return (
      <div className="TranslateForm">
        <div onClick={this.findWord}>
          {this.state.input.slice((cpg - 1) * page_size, cpg * page_size)}
        </div>

        {/* <Modal
          show={this.state.openModal}
          handleHideModal={this.handleHideModal}
        >
          {this.state.output}>
        </Modal> */}

        <div>
          {/* <button onClick={this.openModal}>Open Modal</button> */}
          <Modal
            className="modal_translate2"
            isOpen={this.state.modalIsOpen}
            onAfterOpen={this.afterOpenModal}
            onRequestClose={this.closeModal}
            contentLabel="Example Modal"
          >
            {/* <h2 ref={subtitle => (this.subtitle = subtitle)}>Hello</h2> 
            <button onClick={this.closeModal}>close</button> */}
            <div> {this.state.output}</div>
          </Modal>
        </div>

        <Pagination
          items={this.state.input.length}
          activePage={1}
          maxButtons={8}
          onSelect={e => {
            console.log("select", e);
            this.select(e);
          }}
        />
      </div>
    );
  }
}

export default App;



